<template>
    <div class="p-fluid">
        <div class="p-field">
            <label>Group Id</label>
            <p-input-text v-model="form.gId" type="text" />
        </div>
        <div class="p-field">
            <label>Group Name</label>
            <p-input-text v-model="form.groupName" type="text" />
        </div>
        <div class="p-field">
            <p-checkbox v-model="form.active" :binary="true" />
            <label class="p-ml-2 p-mb-0">Active</label>
        </div>
    </div>
</template>

<script>
import { onMounted, onUnmounted, reactive } from "vue";
import { emitter } from "@/event-bus";
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";
export default {
    props: {
        isUpdate: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const store = useStore();
        const router = useRouter();
        const route = useRoute();
        const form = reactive({
            gId: null,
            groupName: null,
            active: false
        });

        const onSave = () => {
            store
                .dispatch("maintanance/saveUserGroup", { ...form })
                .then(() => {
                    router.push("/maintanance/groups");
                })
                .catch(() => {});
        };

        onMounted(() => {
            emitter.on("user-group:save", onSave);

            if (props.isUpdate) {
                store
                    .dispatch("maintanance/getUserGroup", route.params.id)
                    .then((data) => {
                        form.gId = data.gId;
                        form.groupName = data.groupName;
                        form.active = data.active;
                    })
                    .catch(() => {});
            }
        });

        onUnmounted(() => {
            emitter.off("user-group:save", onSave);
        });

        return {
            form
        };
    }
};
</script>
