<template>
    <p-card style="width: 500px; margin: auto;">
        <template #title>
            Update group
        </template>
        <template #content>
            <user-group-form :isUpdate="true" />
        </template>
        <template #footer>
            <p-button icon="pi pi-check" label="Save" @click="save" />
        </template>
    </p-card>
</template>

<script>
import UserGroupForm from "@/components/maintenance/group-form.vue";
import { emitter } from "@/event-bus";
import { onMounted, ref } from "vue";
import { useRoute } from "vue-router";
import { useStore } from "vuex";

export default {
    components: {
        UserGroupForm
    },
    setup() {
        const route = useRoute();
        const store = useStore();

        const userGroup = ref();

        return {
            userGroup,
            save: () => {
                emitter.emit("user-group:save");
            }
        };
    }
};
</script>
